import { Item } from './Item';

let index = 0;

function rollDice(sides: number): number {
    const result =  Math.floor(Math.random() * sides) + 1;
    console.log(`Rolling a ${sides} sided dice. Result: ${result}`);
    return result; 
}

function addItem(items: Array<Item>, name: string, value: number = 0): Array<Item> {
    console.log(`Adding item: ${name} with value: ${value}`);
    items.push(new Item(index++, name, value));
    return items;

}

export function calculateItems(experience: number): Array<Item> {    
    index = 0;
    let gold = experience * (rollDice(3) + 1);
    console.log("Initial gold: " + gold);
    let items: Array<Item> = [];
    const jewleryProbability = Math.floor(gold / 100);
    console.log(`Will roll: ${jewleryProbability} for items`);
    
    for (let i = 0; i < jewleryProbability; i++) {
        if (rollDice(10) === 10) {
            console.log("Great, Replacing 100 gold with an item");
            gold -= 100;
            console.log("Decreasing gold: " + gold);
            console.log("Rolling for simple gem or simple magical item");
            if (rollDice(20) < 20) {
                console.log("Rolling for simple gem");
                let goldValue = 0;
                switch (rollDice(4)) {
                    case 1:
                        goldValue = rollDice(6);
                        items = addItem(items,"  ", goldValue);                        
                        break;
                    case 2:
                        goldValue = rollDice(100) + 25;
                        items = addItem(items,"  ", goldValue);
                        break;
                    case 3:
                        goldValue = rollDice(100) + 75;
                        items = addItem(items,"  ", goldValue);
                        break;
                    case 4:
                        goldValue = rollDice(100) * 10;
                        items = addItem(items,"  ", goldValue);
                        break;
                }

            } else {
                console.log("Rolling for simple magical item");
                let diceRoll = 0;
                switch (rollDice(4)) {
                    case 1:
                        console.log("Rolling for potion");
                        switch (rollDice(20)) {
                            case 1:
                                items = addItem(items,"  ");
                                break;
                            case 2:
                                items = addItem(items,"  ");
                                break;
                            case 3:
                                items = addItem(items, " ");
                                break;
                            case 4:                                
                                items = addItem(items, "  ");
                                break;
                            case 5:
                            case 6:                                
                                items = addItem(items, "  ");
                                break;
                            case 7:
                                items = addItem(items, "  ");
                                break;
                            case 8:
                            case 9:                                
                                items = addItem(items, " ");
                                break;
                            case 10:
                                items = addItem(items, " ");
                                break;
                            case 11:
                            case 12:
                                items = addItem(items, "  ")
                                break;
                            case 13:
                            case 14:
                                items = addItem(items, " ");
                                break;
                            case 15:                                
                                items = addItem(items, " ");
                                break;
                            default:
                                items = addItem(items, " ");
                               
                        }
                        break;
                    case 2:
                        console.log("Rolling for scroll");
                        
                        switch (rollDice(20)) {
                            case 1:                                
                                items = addItem(items, "  ,  ");
                                break;
                            case 2:
                                items = addItem(items, "  ,  12");
                                break;
                            case 3:
                                items = addItem(items, "  ,  1");
                                break;
                            case 4:
                                items = addItem(items, "  ,  12");
                                break;
                            case 5:
                            case 6:
                                items = addItem(items, " .     ");
                                break;
                            case 7:
                                items = addItem(items, " .     ");
                                break;
                            case 8:
                                items = addItem(items, "  ,  ");
                                break;
                            case 9:
                                items = addItem(items, " ,     ");
                                break;
                            case 10:
                                items = addItem(items, "  ,  13");
                                break;
                            case 11:
                                items = addItem(items, " .     ");
                                break;
                            case 12:
                                items = addItem(items, " .   ");
                                break;
                            case 13:
                                diceRoll = rollDice(2);
                                items = addItem(items, `${diceRoll}   ,   `);
                                break;
                            case 14:
                                items = addItem(items, "  ,  1");
                                break;
                            case 15:
                                diceRoll = rollDice(3);
                                items = addItem(items, `${diceRoll}   ,  `);
                                break;
                            case 16:
                                items = addItem(items, " ,     ");
                                break;
                            case 17:
                                items = addItem(items, " .     ");
                                break;
                            case 18:
                                items = addItem(items, "  ,  12");
                                break;
                            case 19:
                                items = addItem(items, "  ,  12");
                                break;
                            case 20:
                                items = addItem(items, "  ,  13");
                                break;
                        }
                        break;
                    case 3:
                        console.log("Rolling for magical weapon");
                        diceRoll = rollDice(100);
                        if (diceRoll >= 1 && diceRoll <= 10) {
                            items = addItem(items, " +1");
                        } else if (diceRoll === 11) {
                            items = addItem(items, "  1.  2   ");
                        } else if (diceRoll === 12) {
                            items = addItem(items, "  1.  2   ");
                        } else if (diceRoll === 13 || diceRoll === 14) {
                            items = addItem(items, "  1.  2  ");
                        } else if (diceRoll === 15) {
                            items = addItem(items, "  1.      ");
                        } else if (diceRoll >= 16 && diceRoll <= 20) {
                            items = addItem(items, "  2");
                        }
                        else if (diceRoll >= 21 && diceRoll <= 22) {
                            items = addItem(items, "  2,  3   ");
                        }
                        else if (diceRoll >= 23 && diceRoll <= 24) {
                            items = addItem(items, "  2,  3   ");
                        }
                        else if (diceRoll === 25) {
                            items = addItem(items, "  2,      ");
                        }
                        else if (diceRoll >= 26 && diceRoll <= 35) {
                            items = addItem(items, ' "  1');
                        }
                        else if (diceRoll >= 36 && diceRoll <= 40) {
                            items = addItem(items, ' "  2');
                        }
                        else if (diceRoll >= 41 && diceRoll <= 44) {
                            items = addItem(items, "  1 - ");
                        }
                        else if (diceRoll >= 45 && diceRoll <= 46) {
                            items = addItem(items, "   1 - ");
                        }
                        else if (diceRoll >= 47 && diceRoll <= 50) {
                            items = addItem(items, "   1 - ");
                        }
                        else if (diceRoll >= 51 && diceRoll <= 60) {
                            items = addItem(items, "  1");
                        }
                        else if (diceRoll >= 61 && diceRoll <= 65) {
                            items = addItem(items, "  2");
                        }
                        else if (diceRoll >= 66 && diceRoll <= 78) {
                            items = addItem(items, "  1");
                        }
                        else if (diceRoll >= 79 && diceRoll <= 85) {
                            items = addItem(items, "  2");
                        }
                        else if (diceRoll >= 86 && diceRoll <= 90) {
                            items = addItem(items, "   1");
                        }
                        else if (diceRoll >= 91 && diceRoll <= 93) {
                            items = addItem(items, ' "   1');
                        }
                        else if (diceRoll >= 94 && diceRoll <= 95) {
                            items = addItem(items, "     1");
                        }
                        else if (diceRoll >= 96 && diceRoll <= 100) {
                            items = addItem(items, "   1");
                        }
                        break;
                    case 4:
                        console.log("Scroll for magical item");
                        switch (rollDice(20)) {
                            case 1:
                            case 2:
                                items = addItem(items, '   1');
                                break;
                            case 3:
                            case 4:
                                items = addItem(items, '   ');
                                break;
                            case 5:
                            case 6:
                                items = addItem(items, '  ');
                                break;
                            case 7:
                            case 8:
                                items = addItem(items, '  ');
                                break;
                            case 9:
                                items = addItem(items, '   ()');
                                break;
                            case 10:
                                items = addItem(items, '     ()');
                                break;
                            case 11:
                                items = addItem(items, '  ()');
                                break;
                            case 12:
                                items = addItem(items, '  (,)');
                                break;
                            case 13:
                                items = addItem(items, '  ()');
                                break;
                            case 14:
                            case 15:
                                items = addItem(items, '  +1 ()');
                                break;
                            case 16:
                                items = addItem(items, '  ()');
                                break;
                            case 17:
                                items = addItem(items, '  ()');
                                break;
                            case 18:
                                items = addItem(items, '  ()');
                                break;
                            case 19:
                                items = addItem(items, '  ()');
                                break;
                            case 20:
                                items = addItem(items, '   ()');
                                break;

                        }
                        break;

                }
            }

        }
    }
    items = addItem(items, "", gold);    
    console.log(items);
    return items;


}